.tarea-contenedor{
  font-size: .9rem;
  min-height: 50px;
  max-width: 600px;    
  width: 85%;  
  border: 2px solid #ebf0fd;
  border-radius: 10px;
  background-color: #53626eb4;    
  display: flex;    
  justify-content: space-between;
  align-items: center;  
  cursor: pointer;
  margin: 5px 0;  
  transition: all .3s;
}

.tarea-contenedor:hover{
  width: 90%;
  min-height: 54px;  
}


.tarea-texto p{
  overflow: hidden;
  word-wrap: break-word;
  color: #ebf0fd;
}

.tarea-texto{
  padding: 5px 10px;
  width: 90%;  
  display: flex;
  justify-content: left;
  align-items: center;  
}
.tarea-icono{
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ebf0fd;
  font-size: 1.5rem;
}

.complete{
  background-color: #7d6796 !important;  
  opacity: 0.2;
  text-decoration: line-through;
  color: black;
  border: 1px solid #ffffff;
}

@media screen and (max-width: 1024px){
  .tarea-contenedor:hover{
    width: 85;
    min-height: 50px; 
  }
}

